/**
 * @file get transition attr
 * @date 2021-11-28
 * @author xuejie.he
 * @lastModify xuejie.he 2021-11-28
 */
var toMs = function (s) { return (s ? Number(s.slice(0, -1)) * 1000 : 0); };
var getTimeout = function (delays, durations) {
    return delays.length > durations.length
        ? Math.max.apply(Math, delays.map(function (item, n) { return toMs(item) + toMs(durations[n]); })) : Math.max.apply(Math, durations.map(function (item, n) { return toMs(item) + toMs(delays[n]); }));
};
/**
 *
 * @param {HTMLElement} el
 * @returns {timeout: number;propCount: number;}
 */
export var getTransitionAttr = function (el) {
    var styles = window.getComputedStyle(el, null);
    var transitionDelays = styles.transitionDelay.split(", ");
    var transitionDurations = styles.transitionDuration.split(", ");
    var transitionTimeout = getTimeout(transitionDelays, transitionDurations);
    var timeout = 0;
    var propCount = 0;
    if (transitionTimeout > 0) {
        timeout = transitionTimeout;
        propCount = transitionDurations.length;
    }
    return {
        timeout: timeout,
        propCount: propCount,
    };
};
