/**
 * 深克隆
 */
export var deepCloneData = function (data) {
    if (data == null) {
        return data;
    }
    return JSON.parse(JSON.stringify(data));
};
export var getScrollValue = function () {
    var x = window.scrollX || window.pageXOffset;
    var y = window.scrollY || window.pageYOffset;
    var node = document.documentElement || document.body.parentNode;
    if (!x) {
        x = (typeof node.scrollLeft === "number" ? node : document.body).scrollLeft;
    }
    else if (!y) {
        y = (typeof node.scrollTop === "number" ? node : document.body).scrollTop;
    }
    return {
        x: x,
        y: y,
    };
};
