import "../../Portal/style.scss";
export var getTransitionClass = function (x, y, direction) {
    var str = direction.slice(0, 1) + x + y;
    return {
        enter: {
            active: "kite_".concat(str, "EnterActive"),
            to: "kite_".concat(str, "EnterTo"),
            from: "kite_".concat(str, "EnterFrom"),
        },
        leave: {
            active: "kite_".concat(str, "LeaveActive"),
            to: "kite_".concat(str, "LeaveTo"),
            from: "kite_".concat(str, "LeaveFrom"),
        },
    };
};
